@charset "UTF-8";
/* 共通
----------------------------------------- */
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  color: #333333;
  font-size: 16px;
  font-size: 1.6rem;
  font-family: "HelveticaNeue", "Helvetica", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", メイリオ, Verdana, "ＭＳ Ｐゴシック", sans-serif;
  line-height: 1.8;
  letter-spacing: 0.08em;
  background-color: #ffffff;
}

img {
  max-width: 100%;
}

/* Mobile
----------------------------------------- */
.pc {
  display: none;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  z-index: 10;
  background-color: #fff;
  border-bottom: 2px solid #aaaaaa;
  box-shadow: 0 1px 6px 2px rgba(110, 110, 110, 0.3);
}

header h1 {
  position: absolute;
  left: 5%;
  top: 8px;
  width: 16px;
}

header .trigger_wrap {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.15);
  text-align: center;
  border-radius: 4px;
  z-index: 1;
}

header .trigger_wrap .menu_trigger {
  position: relative;
  top: 14px;
  width: 24px;
  height: 20px;
  display: inline-block;
  transition: all 0.4s;
}

header .trigger_wrap .menu_trigger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #aaaaaa;
  border-radius: 4px;
  transition: all 0.4s;
}

header .trigger_wrap .menu_trigger span:nth-of-type(1) {
  top: 0;
}

header .trigger_wrap .menu_trigger span:nth-of-type(2) {
  top: 9px;
}

header .trigger_wrap .menu_trigger span:nth-of-type(3) {
  bottom: 0;
}

header .trigger_wrap .menu_trigger.active span:nth-of-type(1) {
  transform: translateY(9px) rotate(-45deg);
}

header .trigger_wrap .menu_trigger.active span:nth-of-type(2) {
  opacity: 0;
}

header .trigger_wrap .menu_trigger.active span:nth-of-type(3) {
  transform: translateY(-9px) rotate(45deg);
}

header nav .sp {
  position: absolute;
  top: 100%;
  width: 100%;
  font-size: 0;
  background-color: rgba(110, 110, 110, 0.75);
  display: none;
}

header nav .sp li {
  position: relative;
  display: block;
}

header nav .sp li a {
  color: #fff;
  font-weight: bold;
  font-size: 40px;
  font-size: 4rem;
  text-align: right;
  display: block;
  padding: 5px;
  padding-right: 45px;
  text-decoration: none;
}

header nav .sp li a.present::after, header nav .sp li a.in_single_work::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 20px;
  height: 2px;
  background: #fff;
}

.cover_area {
  position: relative;
  width: 100%;
  height: 180px;
  margin-top: 50px;
}

.cover_area .name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  width: 85px;
  height: 85px;
  background-color: rgba(110, 110, 110, 0.6);
  border: 2px solid #fff;
}

.cover_area .name p {
  color: #fff;
  font-size: 13px;
  font-size: 1.3rem;
  font-weight: bold;
  line-height: 1;
  letter-spacing: 0.05em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding-left: 5px;
}

.home_img {
  background: url(../images/top_img.jpg) no-repeat center/cover;
}

.work_img {
  background: url(../images/works_img.jpg) no-repeat center/cover;
}

.bg_wrap {
  width: 100%;
  padding: 0 5px;
  background-color: #fff;
  border: 2px solid #aaaaaa;
}

.bg_wrap .container {
  position: relative;
  width: 100%;
}

.bg_wrap .container .bg-line {
  position: absolute;
  left: 115px;
  top: 0;
  bottom: 0;
  width: 2px;
  margin-top: 0;
  background: #aaaaaa;
  z-index: 0;
}

.section_title {
  color: #aaaaaa;
  font-size: 40px;
  font-size: 4rem;
  font-weight: bold;
  position: relative;
  top: 20px;
  left: 15px;
  display: inline-block;
}

.section_title::after {
  color: #fff;
  content: attr(title);
  position: absolute;
  width: 100%;
  top: 3px;
  left: 3px;
  text-shadow: #aaaaaa 0 1px 1px, #aaaaaa 0 -1px 1px, #aaaaaa -1px 0 1px, #aaaaaa 1px 0 1px;
  background-color: #fff;
  z-index: -1;
}

section {
  padding: 50px 0;
  position: relative;
  z-index: 2;
}

.bd {
  position: relative;
  border: 2px solid transparent;
  border-radius: 4px;
}

.bd::after {
  position: absolute;
  left: 3px;
  top: 3px;
  width: 100%;
  height: 100%;
  content: '';
  box-sizing: content-box;
  background-color: #fff;
  border: 2px solid #aaaaaa;
  border-radius: 4px;
  z-index: -2;
}

.bd .draw {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.bd .draw::before, .bd .draw::after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  width: 0;
  height: 0;
  border: 2px solid transparent;
  border-radius: 4px;
  z-index: -1;
}

.bd .draw_active {
  z-index: -1;
}

.bd .draw_active::before, .bd .draw_active::after {
  width: 100%;
  height: 100%;
  z-index: -1;
}

.bd .draw_active::before {
  border-top-color: #333;
  border-right-color: #333;
  transition: width 0.5s ease-out, height 0.5s ease-out 0.5s;
}

.bd .draw_active::after {
  border-bottom-color: #333;
  border-left-color: #333;
  transition: height 0.5s ease-out, width 0.5s ease-out 0.5s;
}

#about .about_wrap {
  margin-top: 70px;
}

#about .about_wrap .logo {
  position: relative;
  left: 0;
  width: 90px;
  height: 90px;
  background-color: #fff;
  border: 2px solid #aaaaaa;
  border-radius: 50%;
  z-index: 3;
}

#about .about_wrap .logo img {
  position: absolute;
  width: 23px;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

#about .about_wrap .about_content {
  margin: -35px 18px 0 30px;
  padding: 33px 30px 30px 33px;
}

#about .about_wrap .about_content p {
  margin-top: 20px;
}

#about .about_wrap .about_content p.name {
  font-size: 18px;
  font-size: 1.8rem;
}

#skills .skill_wrap {
  margin-top: 90px;
}

#skills .skill_wrap .skill_line {
  display: none;
}

#skills .skill_wrap .skill_content {
  margin: 30px 18px 0 30px;
  padding: 38px 30px 30px 33px;
}

#skills .skill_wrap .skill_content h3 {
  font-weight: bold;
  position: absolute;
  top: -25px;
  left: -25px;
  padding: 8px 8px 8px 0;
  background: #fff;
  display: inline-block;
  z-index: 2;
}

#works::after {
  content: '';
  display: block;
  clear: both;
}

#works article {
  margin: 70px 13px 0 10px;
  padding: 10px 15px 15px 18px;
}

#works article .post_img {
  margin: 8px 0;
}

#works article .post_img img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  border-radius: 4px;
}

#works article time {
  font-size: 14px;
  font-size: 1.4rem;
  color: #666666;
  margin: 20px 0;
}

#works article h1 {
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: bold;
  position: relative;
  margin: 8px 0 0;
  padding-bottom: 15px;
  border-bottom: 2px solid #333333;
}

#works article h1 a {
  color: #333333;
  text-decoration: none;
}

#works article .tags {
  position: relative;
  margin: 5px 0 0;
  overflow: hidden;
}

#works article .tags a {
  font-size: 13px;
  font-size: 1.3rem;
  color: #666666;
  text-decoration: none;
  display: inline-block;
  margin: 2px;
  padding: 2px;
  border: 1px solid #666666;
  border-radius: 4px;
}

#works .to_list {
  margin-top: 70px;
}

#works .to_list .to_list_btn {
  color: #fff;
  font-size: 18px;
  font-size: 1.8rem;
  display: inline-block;
  position: absolute;
  right: 13px;
  padding: 3px 50px;
  text-decoration: none;
  background-color: #aaaaaa;
  border-radius: 7px;
}

#works .to_list .to_list_btn::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 3px;
  width: 100%;
  height: 100%;
  background-color: #777777;
  border-radius: 7px;
  z-index: -1;
}

#works .to_list .to_list_btn:active {
  transform: translate(3px, 3px);
}

#works .to_list .to_list_btn:active::after {
  content: none;
}

#works article.main {
  color: #333333;
  margin-bottom: 100px;
  padding: 30px 15px 50px 18px;
}

#works article.main .main_content {
  margin-top: 30px;
}

#works article.main .main_content h2 {
  color: #333333;
  font-size: 20px;
  font-size: 2rem;
  font-weight: bold;
  margin-top: 30px;
}

#works article.main .main_content h3 {
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: bold;
  margin-top: 20px;
}

#works article.main .main_content strong {
  font-weight: bold;
  margin-top: 15px;
}

#works article.main .main_content p {
  margin-top: 10px;
}

#works article.main .main_content p.right {
  text-align: right;
}

#works article.main .main_content a {
  color: #333333;
}

#works article.main .main_content a:hover {
  color: #aaaaaa;
}

#works article.main .main_content a:visited {
  color: #777777;
}

#works article.main .main_content ul {
  list-style: disc;
  padding: 1em 0 1em 2em;
}

#works article.main .main_content ol {
  list-style: decimal;
  padding: 1em 0 1em 2em;
}

#works article.main .main_content img {
  margin: 10px 0;
}

#works .prev_next {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
}

#works .prev_next div {
  margin: 10px 0;
  margin-right: 13px;
  text-align: center;
}

#works .prev_next div a {
  color: #fff;
  font-size: 18px;
  font-size: 1.8rem;
  display: inline-block;
  position: relative;
  width: 200px;
  padding: 3px 0;
  text-decoration: none;
  background-color: #aaaaaa;
  border-radius: 7px;
}

#works .prev_next div a::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 3px;
  width: 100%;
  height: 100%;
  background-color: #777777;
  border-radius: 7px;
  z-index: -1;
}

#works .prev_next div a:active {
  transform: translate(3px, 3px);
}

#works .prev_next div a:active::after {
  content: none;
}

#works .prev_next .leftcol a {
  padding-right: 15px;
}

#works .prev_next .rightcol a {
  padding-left: 15px;
}

#contact .contact_form {
  margin: 70px 13px 0 10px;
  padding: 3px 0 0 3px;
}

#contact .contact_form form {
  position: relative;
  width: 80%;
  margin: 30px auto;
  z-index: 2;
}

#contact .contact_form form label {
  font-size: 14px;
  font-size: 1.4rem;
  display: block;
  margin: 30px 0 3px;
}

#contact .contact_form form input,
#contact .contact_form form textarea {
  font-size: 16px;
  font-size: 1.6rem;
  width: 100%;
  padding: 8px;
  border: 2px solid #333;
  border-radius: 4px;
  outline: none;
}

#contact .contact_form form p.submit {
  color: #fff;
  margin: 20px 0 0;
  position: relative;
  width: calc(100% - 3px);
  padding: 0;
  background-color: #aaaaaa;
  border: none;
  border-radius: 7px;
  outline: none;
}

#contact .contact_form form p.submit::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 3px;
  width: 100%;
  height: 100%;
  background-color: #777777;
  border-radius: 7px;
  z-index: -1;
}

#contact .contact_form form p.submit:active {
  transform: translate(3px, 3px);
}

#contact .contact_form form p.submit:active::after {
  content: none;
}

#contact .contact_form form p.submit input[type='submit'] {
  color: #fff;
  font-size: 18px;
  font-size: 1.8rem;
  padding: 5px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

#contact .contact_form form p.submit .ajax-loader {
  position: absolute;
}

footer {
  position: relative;
  bottom: 8px;
  margin-top: 100px;
  text-align: center;
  z-index: 1;
}

footer small {
  font-size: 13px;
  font-size: 1.3rem;
  letter-spacing: 0.05em;
  padding: 8px;
  background-color: #fff;
}

.wp-pagenavi {
  float: right;
  margin: 70px 13px 0 10px;
  padding: 10px 0;
  text-align: center;
  background-color: #ffffff;
  z-index: 1;
}

.wp-pagenavi span,
.wp-pagenavi a {
  color: #333333;
  text-decoration: none;
  display: inline-block;
  margin: 3px;
  padding: 2px 13px;
  border: 2px solid #333;
  border-radius: 4px;
}

.wp-pagenavi span.current,
.wp-pagenavi a.current {
  color: #fff;
  background-color: #aaaaaa;
  border: 2px solid #aaaaaa;
}

