    left: 62%;
    width: 38%;
  }
  #skills .skill_wrap .b_e {
    left: 70%;
    width: 30%;
  }
  #works article,
  #contact .contact_form {
    margin: 70px 0 0 60px;
  }
}

/* フェードインアニメーション */
.fade {
  transform: translate(30px, 10px);
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
}

.fade_in {
  transform: translate(0, 0);
  visibility: visible;
  opacity: 1;
  transition: 1s transform, 1s opacity;
}
